.commit-timeline-section {
  width: 100%;
  margin: 0;
  padding: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  background: #ffffff;
}

.commit-timeline-container {
  display: flex;
  flex-wrap: wrap;
  align-items: stretch;
  justify-content: center;
  max-width: 75rem; /* 1200px */
  margin: 0 auto 0 auto;
  min-height: 21.25rem; /* 340px */
  gap: 0;
}

.commit-content {
  flex: 1;
  padding: 3.5rem 0 3.5rem 3rem; /* 56px 0 56px 48px */
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
  justify-content: center;
}

.commit-label {
  color: #b0b4bb;
  font-size: 1rem;
  margin-bottom: 1.2rem;
  font-weight: 500;
}

.commit-title {
  font-size: 2rem;
  font-weight: 800;
  color: #5d5d5d;
  margin-bottom: 1.2rem;
  line-height: 1.15;
}

.commit-desc {
  color: #a7abb6;
  font-size: 1rem;
  line-height: 1.7;
  max-width: 25.9375rem; /* 415px */
  font-weight: 500;
}

.commit-timeline {
  flex: 1.55;
  min-width: 6%;
  padding: 3rem 3rem 3rem 0; /* 48px 48px 48px 0 */
  display: flex;
}

.timeline {
  width: 100%;
  background: transparent;
  box-shadow: none;
}

.timeline-content {
  display: flex;
  align-items: center;
}

.timeline-container {
  align-items: center;
}
.timeline-header {
  padding: 1.25rem; /* 20px */
  display: flex;
  gap: 3.75rem; /* 60px */
  /* margin-bottom: 1.2rem; */
}

.timeline-grid {
  position: relative;
  display: flex;
  flex-direction: column;
  gap: 4rem;
  min-height: 13.75rem; /* 220px */
}

.timeline-labels {
  display: grid;
  grid-template-columns: repeat(12, 1fr);
  color: #e7e7e7;
  font-size: 1rem;
  position: relative;
  z-index: 2;
  font-weight: 400;
}

.timeline-labels span {
  text-align: right;
  color: #e7e7e7;
}

.timeline-lines {
  position: absolute;
  top: 1.5rem;
  left: 0;
  width: 100%;
  height: calc(100% - 2rem); /* 32px */
  display: grid;
  grid-template-columns: repeat(12, 1fr);
  z-index: 1;
  pointer-events: none;
}
.timeline-container {
  font-family: sans-serif;
  padding: 1.25rem; /* 20px */
}

.timeline-header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 1rem; /* 16px */
}

.legend .dot {
  width: 1.125rem; /* 18px */
  height: 1.125rem;
  display: inline-block;
  border-radius: 50%;
  margin: 0 0.375rem; /* 6px */
}

.completed {
  background-color: #1d4ed8;
}

.in-progress {
  display: inline-block;
  border-radius: 50%;
  vertical-align: middle;
  border: 0.15625rem solid #c2c2c2; /* 2.5px */
}

.timeline-line {
  border-right: 0.125rem dashed #e5e7eb; /* 2px */
  height: 100%;
  width: 100%;
  margin: 0 auto;
}

.timeline-line.blue {
  border-right: 0.1875rem solid #2563eb; /* 3px */
  background: none;
  z-index: 2;
}

.timeline-tasks {
  display: grid;
  grid-template-columns: repeat(12, 1fr);
  position: relative;
  min-height: 11.25rem; /* 180px */
  z-index: 3;
}

.timeline-task {
  display: flex;
  align-items: center;
  gap: 0.1rem;
  border-radius: 999px;
  padding: 0.7rem 2.2rem 0.7rem 1.2rem;
  position: absolute;
  height: 3.5rem; /* 56px */
  box-shadow: 0 0.5rem 2rem rgba(37, 99, 235, 0.08); /* 8px 32px */
  font-size: 0.5rem;
  background: #f8faff;
  color: #222;
  top: 0;
  transition: box-shadow 0.2s;
}

.timeline-task.done {
  background: #2563eb;
  color: #fff;
  top: 0;
  z-index: 2;
  box-shadow: 0 0.5rem 2rem rgba(37, 99, 235, 0.18); /* 8px 32px */
}

.timeline-task.progress {
  background: #fff;
  color: #222;
  top: 4.375rem; /* 70px */
  box-shadow: 0 0.5rem 2rem rgba(37, 99, 235, 0.08); /* 8px 32px */
}

.timeline-task.progress:last-child {
  top: 8.75rem; /* 140px */
}

.timeline-task .percent {
  background: #4d7aee1a;
  color: #4d7aee;
  border-radius: 1rem; /* 16px */
  padding: 0.2rem 1.1rem;
  margin-right: 0.7rem;
  font-size: 1rem;
  box-shadow: 0 0.125rem 0.5rem rgba(37, 99, 235, 0.1); /* 2px 8px */
}

.timeline-task.done .percent {
  background: #4d7aee;
  color: #fff;
  box-shadow: none;
  box-shadow: 0 0.125rem 0.5rem rgba(37, 99, 235, 0.1); /* 2px 8px */
}

.timeline-task .task-name {
  font-weight: 400;
  margin-right: 0.2rem;
  font-size: 0.7rem;
}

.timeline-task .avatars {
  display: flex;
  align-items: center;
  margin-left: 1.2rem;
}

.timeline-task .avatars img {
  width: 2.25rem; /* 36px */
  height: 2.25rem;
  border-radius: 50%;
  border: 0.15625rem solid #fff; /* 2.5px */
  margin-left: -1.125rem; /* -18px */
  object-fit: cover;
  background: #e0e0e0;
  box-shadow: 0 0.125rem 0.5rem rgba(37, 99, 235, 0.08); /* 2px 8px */
}

