/*-------------------------------- Hiu ng cun nh lin tc -------------------------------- */

.marquee-wrapper {
  overflow: hidden;
  width: 100%;
  background: #fff;
  margin-bottom: 16px;
}
.marquee {
  width: 100%;
  white-space: nowrap;
  box-sizing: border-box;
}
.marquee-normal {
  margin-bottom: 20px;
}
.marquee-group {
  display: flex;
  align-items: center;
  animation: marquee 64s linear infinite;
  min-width: max-content;
}
.marquee-img {
  height: 220px;
  width: auto;
  margin-right: 24px;
  border-radius: 16px;
  object-fit: cover;
  display: block;
}
@keyframes marquee {
  0% {
    transform: translateX(0);
  }
  100% {
    transform: translateX(-50%);
  }
}
.marquee-reverse .marquee-group {
  animation: marquee-reverse 32s linear infinite;
}
@keyframes marquee-reverse {
  0% {
    transform: translateX(-50%);
  }
  100% {
    transform: translateX(0);
  }
}

