  margin-bottom: 0;
  font-weight: 400;
}

.achievements-carousel-wrapper {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  max-width: 90rem;
  margin: 0;
  position: relative;
  gap: 2.5rem;
}

.achievements-carousel {
  width: 100%;
  max-width: 90rem;
}

.achievement-slide {
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 0 0.5rem;
}
.achievement-visual {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-bottom: 1.5rem;
  min-height: 4rem; /* m bo  ch cho quote */
}
.big-quote-svg {
  position: absolute;
  left: -23rem; /* iu chnh khong cch xa logo, c th tng/gim */
  top: 50%;
  transform: translateY(-50%);
  width: 4rem;
  height: 4rem;
  opacity: 5;
  display: block;
  margin: 0;
  pointer-events: none;
}

.achievement-logo {
  margin: 0 auto;
  z-index: 1;
}

.achievement-logo img {
  height: 3.75rem;
  margin: 0;
  object-fit: contain;
}

.achievement-content {
  font-size: 1.5rem;
  color: #555;
  text-align: center;
  align-items: center;
  justify-content: center;
  margin-bottom: 2rem;
  margin-top: 0.5rem;
  line-height: 1.5;
  font-weight: 400;
  max-width: 70rem;
}

.achievement-author {
  text-align: center;
}

.author-name {
  color: #3ba3ff;
  font-size: 1.25rem;
  font-weight: 700;
  margin-bottom: 0.125rem;
}

.author-role {
  color: #7e8ca0;
  font-size: 1rem;
  margin-top: 0.25rem;
  font-weight: 400;
}

.achievements-arrow {
  width: 3rem;
  height: 3rem;
  border-radius: 50%;
  border: 2px solid #2563eb;
  background-color: #fff;
  color: #2563eb;
  font-size: 1.5rem;
  line-height: 1;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  transition:
    background-color 0.3s,
    color 0.3s,
    box-shadow 0.2s;
  box-shadow: none;
}

.achievements-arrow:hover {
  background: #f3f7ff;
  box-shadow: 0 0.5rem 2rem rgba(37, 99, 235, 0.13); /* 8px 32px */
}

.achievements-arrow.left {
  order: 0;
}

.achievements-arrow.right {
  order: 0;
}
/*-------------------------------------- Bn ng hnh --------------------------------------*/
.companion-section {
  width: 100%;
  display: flex;
  padding-bottom: 2.5rem; /* 40px */
  justify-content: center;
  background: #ffffff;
}

.companion-content {
  background: #3976ff;
  border-radius: 2rem;
  width: 95%;
  max-width: 71.25rem; /* 1140px */
  min-height: 11.25rem; /* 180px */
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 2.5rem 3rem; /* 40px 48px */
  box-sizing: border-box;
}
.companion-text {
  width: 42.5rem; /* 680px */
}
.companion-text h2 {
  color: #fff;
  font-size: 2rem;
  font-weight: 700;
  margin-bottom: 1rem; /* 16px */
}

.companion-text p {
  color: #e6edff;
  font-size: 1.1rem;
  margin: 0;
  font-weight: 400;
}

.companion-btn {
  background: #fff;
  color: #2563eb;
  font-weight: 700;
  border: none;
  border-radius: 1rem; /* 16px */
  padding: 1.125rem 2rem; /* 18px 32px */
  font-size: 1rem;
  cursor: pointer;
  transition:
    background 0.18s,
    color 0.18s;
  box-shadow: 0 0.25rem 1.5rem rgba(37, 99, 235, 0.08); /* 4px 24px */
}

.companion-btn:hover {
  background: #e6edff;
  color: #1746a2;
}
