import api from '../config'

// i tng orderService cha cc phng thc  tng tc vi API n hng
const orderService = {
  // Ly danh sch n hng
  async getOrders() {
    try {
      const response = await api.get('/orders') // Gi yu cu GET n API  ly danh sch n hng
      // Ly thng tin dch v cho mi n hng
      const orders = response.data.data || [] // Ly danh sch n hng t phn hi
      const ordersWithService = await Promise.all(
        orders.map(async (order) => {
          try {
            const serviceResponse = await api.get(`/services/${order.serviceId}`) // Gi yu cu GET  ly thng tin dch v
            const service = serviceResponse.data.data
            return {
              ...order,
              serviceType: service?.title || 'Khng xc nh', // Thm loi dch v vo n hng
              paidAmount: service?.price || 0, // Thm s tin  thanh ton vo n hng
            }
          } catch (error) {
            console.error(`Error fetching service for order ${order._id}:`, error) // In ra li nu c
            return {
              ...order,
              serviceType: 'Khng xc nh', // Nu khng ly c dch v, gn loi dch v l 'Khng xc nh'
              paidAmount: 0, // Gn s tin  thanh ton l 0
            }
          }
        })
      )

      return {
        success: true,
        data: ordersWithService, // Tr v danh sch n hng  c thng tin dch v
        message: response.data.message || 'Ly danh sch n hng thnh cng', // Thng ip thnh cng
      }
    } catch (error) {
      console.error('Error fetching orders:', error) // In ra li nu c
      return {
        success: false,
        message: error.response?.data?.message || 'Khng th ti danh sch n hng', // Thng ip li
      }
    }
  },

  // Ly chi tit n hng theo ID
  async getOrderById(id) {
    try {
      const response = await api.get(`/orders/${id}`) // Gi yu cu GET n API vi ID c th
      return {
        success: true,
        data: response.data.data, // Tr v d liu chi tit n hng
        message: response.data.message || 'Ly thng tin n hng thnh cng', // Thng ip thnh cng
      }
    } catch (error) {
      console.error('Error fetching order details:', error) // In ra li nu c
      return {
        success: false,
        message: error.response?.data?.message || 'Khng th ti thng tin n hng', // Thng ip li
      }
    }
  },

  // Cp nht trng thi n hng
  async updateOrderStatus(id, updateData) {
    try {
      const response = await api.put(`/orders/${id}`, updateData) // Gi yu cu PUT  cp nht trng thi n hng
      return {
        success: true,
        data: response.data.data, // Tr v d liu t phn hi
        message: response.data.message || 'Cp nht n hng thnh cng', // Thng ip thnh cng
      }
    } catch (error) {
      console.error('Error updating order status:', error) // In ra li nu c
      return {
        success: false,
        message: error.response?.data?.message || 'Khng th cp nht trng thi n hng', // Thng ip li
      }
    }
  },

  // To n hng mi
  async createOrder(orderData) {
    try {
      // m bo trng thi n hng mi lun l 'pending' (ch x l)
      const orderWithStatus = {
        ...orderData,
        orderStatus: 'pending', // Gn trng thi mc nh l ch x l
      }

      const response = await api.post('/orders', orderWithStatus) // Gi yu cu POST  to n hng mi
      return {
        success: true,
        data: response.data.data, // Tr v d liu t phn hi
        message: response.data.message || 'To n hng thnh cng', // Thng ip thnh cng
      }
    } catch (error) {
      console.error('Error creating order:', error) // In ra li nu c
      return {
        success: false,
        message: error.response?.data?.message || 'Khng th to n hng', // Thng ip li
      }
    }
  },

  // Ly lch s n hng
  async getOrderHistory() {
    try {
      const response = await api.get('/orders/history') // Gi yu cu GET  ly lch s n hng
      return {
        success: true,
        data: response.data.data || [], // Tr v d liu lch s n hng, nu khng c th tr v mng rng
        message: response.data.message || 'Ly lch s n hng thnh cng', // Thng ip thnh cng
      }
    } catch (error) {
      console.error('Error fetching order history:', error) // In ra li nu c
      return {
        success: false,
        message: error.response?.data?.message || 'Khng th ti lch s n hng', // Thng ip li
      }
    }
  },

  // X l thanh ton cho n hng
  async processPayment(orderId, paymentData) {
    try {
      const response = await api.post(`/orders/${orderId}/process-payment`, paymentData) // Gi yu cu POST  x l thanh ton
      return {
        success: true,
        data: response.data.data, // Tr v d liu t phn hi
        message: response.data.message || 'X l thanh ton thnh cng', // Thng ip thnh cng
      }
    } catch (error) {
      console.error('Error processing payment:', error) // In ra li nu c
      return {
        success: false,
        message: error.response?.data?.message || 'Khng th x l thanh ton', // Thng ip li
      }
    }
  },
}

export default orderService // Xut i tng orderService  s dng  ni khc
