import api from '../config'

// i tng authService cha cc phng thc lin quan n xc thc ngi dng
const authService = {
  // Phng thc ng nhp
  async login(credentials) {
    try {
      // Gi yu cu ng nhp n API
      const response = await api.post('/admin/login', credentials)

      // Kim tra xem ng nhp c thnh cng khng
      if (response.data.success) {
        const { token, admin } = response.data
        if (token) {
          // Lu tr token v thng tin admin vo localStorage
          localStorage.setItem('token', token)
          localStorage.setItem('user', JSON.stringify(admin))
          localStorage.setItem('adminToken', token)
          localStorage.setItem('adminInfo', JSON.stringify(admin))
        }
      }

      // Tr v d liu phn hi
      return response.data
    } catch (error) {
      // X l li nu c
      console.error('Login error:', error)
      return {
        success: false,
        message: error.response?.data?.message || 'ng nhp tht bi',
      }
    }
  },

  // Phng thc ng xut
  logout() {
    // Xa thng tin ngi dng v token khi localStorage
    localStorage.removeItem('token')
    localStorage.removeItem('user')
    localStorage.removeItem('adminToken')
    localStorage.removeItem('adminInfo')
  },

  // Phng thc ly thng tin ngi dng hin ti
  getCurrentUser() {
    const userStr = localStorage.getItem('user')
    return userStr ? JSON.parse(userStr) : null
  },

  // Phng thc ly thng tin admin
  getAdminInfo() {
    const adminInfo = localStorage.getItem('adminInfo')
    return adminInfo ? JSON.parse(adminInfo) : null
  },

  // Phng thc kim tra xem ngi dng  xc thc hay cha
  isAuthenticated() {
    return !!localStorage.getItem('token') || !!localStorage.getItem('adminToken')
  },
}

// Xut i tng authService  s dng  ni khc
export default authService
