<template>
  <!-- Container chnh qun l danh sch tin tc -->
  <div class="news-list">
    <!-- Hin th trng thi ang ti -->
    <div v-if="loading" class="loading-container">
      <!-- <div class="loading-spinner"></div> -->
      <p>ang ti danh sch tin tc...</p>
    </div>

    <!-- Hin th khi c li -->
    <div v-else-if="error" class="error-container">
      <p class="error-message">{{ error }}</p>
      <button class="retry-btn" @click="loadNews">Th li</button>
    </div>

    <!-- Ni dung chnh - hin th khi khng c li v  ti xong -->
    <template v-else>
      <!-- Thanh cng c tm kim v lc -->
      <div class="actions-header">
        <div class="actions-left">
          <div class="search-filter">
            <div class="search-box">
              <!-- Icon tm kim -->
              <i class="fas fa-search"></i>
              <input
                type="text"
                v-model="searchQuery"
                placeholder="Tm kim theo tiu ..."
                @input="handleSearch"
              />
            </div>
          </div>

          <!-- New buttons for Insert and Trash -->
          <div class="action-buttons">
            <button class="action-btn add" @click="$router.push('/admin/tin-tuc/them-moi')">
              <i class="fas fa-plus"></i>
              Thm tin tc
            </button>
            <button class="action-btn trash" @click="$router.push('/admin/tin-tuc/thung-rac')">
              <i class="fas fa-trash-alt"></i>
              Thng rc
            </button>
          </div>
        </div>
      </div>

      <!-- Bng danh sch tin tc -->
      <div class="table-container responsive-table">
        <table>
          <thead>
            <tr>
              <th style="width: 70px">
                <input
                  type="checkbox"
                  :checked="isAllSelected"
                  @change="toggleSelectAll"
                  style="cursor: pointer"
                />
              </th>
              <th style="width: 6%">No.</th>
              <th>nh</th>
              <th style="max-width: 250px">Tiu </th>
              <th>Loi</th>
              <th>Tc gi</th>
              <th>Thao tc</th>
            </tr>
          </thead>

          <tbody>
            <!-- Hin th tng dng tin tc -->
            <tr v-for="(news, index) in filteredNews" :key="news._id">
              <td>
                <input
                  type="checkbox"
                  :checked="isSelected(news._id)"
                  @change="toggleSelect(news._id)"
                  style="cursor: pointer"
                />
              </td>
              <td class="news-id">{{ index + 1 }}</td>
              <!-- nh i din tin tc -->
              <td>
                <div class="image-container">
                  <img
                    :src="news.imageUrl"
                    alt="News image"
                    class="news-image"
                    @error="handleImageError($event, news._id)"
                  />
                </div>
              </td>
              <td class="truncate-text" style="max-width: 250px">{{ news.title }}</td>
              <td class="type-cell">{{ news.type }}</td>
              <td>{{ news.author }}</td>
              <td class="actions-cell" style="height: 120px !important">
                <div class="actions">
                  <!-- Nt xem chi tit -->
                  <button class="icon-btn info" @click="showDetails(news)">
                    <i class="fas fa-info-circle"></i>
                  </button>
                  <!-- Nt chnh sa - kim tra quyn sa da vo tc gi -->
                  <button
                    class="icon-btn edit"
                    @click="canEditNews(news) ? openEditModal(news) : showPermissionError('sa')"
                    :class="{ disabled: !canEditNews(news) }"
                    :title="canEditNews(news) ? 'Chnh sa' : 'Bn khng c quyn sa bi vit ny'"
                  >
                    <i class="fas fa-edit"></i>
                  </button>
                  <!-- Nt xa tm thi (chuyn vo thng rc) - kim tra quyn xa -->
                  <button
                    v-if="!news.isDeleted"
                    class="icon-btn delete"
                    @click="
                      canDeleteNews(news) ? confirmSoftDelete(news) : showPermissionError('xa')
                    "
                    :class="{ disabled: !canDeleteNews(news) }"
                    :title="
                      canDeleteNews(news)
                        ? 'Chuyn vo thng rc'
                        : 'Bn khng c quyn xa bi vit ny'
                    "
                  >
                    <i class="fas fa-trash"></i>
                  </button>
                  <!-- Nt khi phc tin  xa - kim tra quyn khi phc -->
                  <button
                    v-else
                    class="icon-btn restore"
                    @click="
                      canEditNews(news) ? confirmRestore(news) : showPermissionError('khi phc')
                    "
                    :class="{ disabled: !canEditNews(news) }"
                    :title="
                      canEditNews(news) ? 'Khi phc' : 'Bn khng c quyn khi phc bi vit ny'
                    "
                  >
                    <i class="fas fa-trash-restore"></i>
                  </button>
                  <!-- Nt xa vnh vin (ch hin th vi tin  xa) -->
                  <button
                    v-if="news.isDeleted"
                    class="icon-btn permanent-delete"
                    @click="
                      canDeleteNews(news)
                        ? confirmPermanentDelete(news)
                        : showPermissionError('xa vnh vin')
                    "
                    :class="{ disabled: !canDeleteNews(news) }"
                    :title="
                      canDeleteNews(news) ? 'Xa vnh vin' : 'Bn khng c quyn xa bi vit ny'
                    "
                  >
                    <i class="fas fa-trash-alt"></i>
                  </button>
                </div>
              </td>
            </tr>
          </tbody>
        </table>
      </div>

      <!-- Modal xem chi tit tin tc -->
      <div class="modal" v-if="showDetailsModal">
        <div class="modal-overlay" @click="showDetailsModal = false"></div>
        <div class="modal-content" @click.stop>
          <div
            class="modal-header"
            style="display: flex; align-items: center; justify-content: space-between"
          >
            <h3>Chi tit tin tc</h3>
            <div class="modal-actions" style="margin-left: auto">
              <button class="close-btn" @click="showDetailsModal = false">
                <i class="fas fa-times"></i>
              </button>
            </div>
          </div>
          <div class="modal-body">
            <!-- Cc trng thng tin chi tit -->
            <div class="detail-item">
              <label>ID:</label>
              <p>{{ selectedNews._id }}</p>
            </div>
            <div class="detail-item">
              <label>nh:</label>
              <img
                :src="selectedNews.imageUrl"
                alt="News image"
                class="detail-image"
              />
            </div>
            <div class="detail-item">
              <label>Tiu :</label>
              <p>{{ selectedNews.title }}</p>
            </div>
            <div class="detail-item">
              <label>Tm tt:</label>
              <p>{{ selectedNews.summary }}</p>
            </div>
            <div class="detail-item">
              <label>Ni dung:</label>
              <p>{{ selectedNews.content }}</p>
            </div>
            <div class="detail-item">
              <label>Loi:</label>
              <p>{{ selectedNews.type }}</p>
            </div>
            <div class="detail-item">
              <label>Tc gi:</label>
              <p>{{ selectedNews.author }}</p>
            </div>
            <div class="detail-item">
              <label>Trng thi:</label>
              <p>
                <span :class="['status-badge', selectedNews.isDeleted ? 'inactive' : 'active']">
                  {{ selectedNews.isDeleted ? ' xa' : ' ng' }}
                </span>
              </p>
            </div>
          </div>
        </div>
      </div>

      <!-- Modal thm/chnh sa tin tc -->
      <div class="modal" v-if="showFormModal">
        <div class="modal-overlay" @click="closeFormModal"></div>
        <div class="modal-content" @click.stop>
          <div class="modal-header">
            <h3>{{ isEditing ? 'Chnh sa tin tc' : 'Thm tin tc mi' }}</h3>
            <button class="close-btn" @click="closeFormModal">
              <i class="fas fa-times"></i>
            </button>
          </div>
          <div class="modal-body">
            <!-- Form nhp/sa thng tin tin tc -->
            <form @submit.prevent="handleSubmit" novalidate>
              <div class="form-group">
                <label>Tiu </label>
                <input
                  type="text"
                  v-model="formData.title"
                  :class="{ error: errors.title }"
                  placeholder="Nhp tiu  tin tc"
                />
                <span class="error-message" v-if="errors.title">{{ errors.title }}</span>
              </div>
              <div class="form-group">
                <label>Phn loi</label>
                <input
                  type="text"
                  v-model="formData.type"
                  :class="{ error: errors.type }"
                  placeholder="Nhp phn loi tin tc"
                  maxlength="50"
                  required
                />
                <span class="error-message" v-if="errors.type">{{ errors.type }}</span>
              </div>
              <div class="form-group">
                <label>nh</label>
                <div class="image-upload" @click="triggerFileInput">
                  <input
                    type="file"
                    accept="image/*"
                    @change="handleImageUpload"
                    ref="fileInput"
                    style="display: none"
                  />
                  <div v-if="imagePreview" class="image-preview" @click.stop>
                    <img :src="imagePreview" alt="Preview" />
                    <button type="button" class="remove-image" @click.stop="removeImage">
                      <i class="fas fa-times"></i>
                    </button>
                  </div>
                  <div v-else class="upload-placeholder">
                    <i class="fas fa-cloud-upload-alt upload-icon"></i>
                    <div class="upload-text">Nhp  ti nh ln</div>
                    <div class="upload-subtext">nh dng: JPG, PNG, GIF</div>
                  </div>
                </div>
              </div>
              <div class="form-group">
                <label>Tm tt</label>
                <textarea
                  v-model="formData.summary"
                  :class="{ error: errors.summary }"
                  placeholder="Nhp tm tt ni dung"
                ></textarea>
                <span class="error-message" v-if="errors.summary">{{ errors.summary }}</span>
              </div>
              <div class="form-group">
                <label>Ni dung chi tit</label>
                <textarea
                  v-model="formData.content"
                  :class="{ error: errors.content }"
                  placeholder="Nhp ni dung chi tit"
                  required
                  rows="6"
                ></textarea>
                <span class="error-message" v-if="errors.content">{{ errors.content }}</span>
              </div>
              <!-- Cc nt hnh ng form -->
              <div class="form-actions">
                <button type="button" class="cancel-btn" @click="closeFormModal">
                  <i class="fas fa-times"></i>
                  Hy
                </button>
                <button type="submit" class="submit-btn">
                  <i class="fas fa-save"></i>
                  {{ isEditing ? 'Cp nht' : 'Thm mi' }}
                </button>
              </div>
            </form>
          </div>
        </div>
      </div>

      <!-- Modal xc nhn xa tm thi -->
      <div class="modal" v-if="showSoftDeleteModal">
        <div class="modal-overlay" @click="showSoftDeleteModal = false"></div>
        <div class="modal-content" @click.stop>
          <div class="modal-header">
            <h3>Xc nhn chuyn vo thng rc</h3>
            <button class="close-btn" @click="showSoftDeleteModal = false">
              <i class="fas fa-times"></i>
            </button>
          </div>
          <div class="modal-body">
            <p>
              Bn c chc chn mun chuyn tin tc "{{ selectedNews.title }}" vo thng rc khng?
            </p>
            <div class="form-actions">
              <button class="cancel-btn" @click="showSoftDeleteModal = false">Hy</button>
              <button class="delete-btn" @click="handleSoftDelete">Chuyn vo thng rc</button>
            </div>
          </div>
        </div>
      </div>

      <!-- Modal xc nhn khi phc -->
      <div class="modal" v-if="showRestoreModal">
        <div class="modal-overlay" @click="showRestoreModal = false"></div>
        <div class="modal-content" @click.stop>
          <div class="modal-header">
            <h3>Xc nhn khi phc</h3>
            <button class="close-btn" @click="showRestoreModal = false">
              <i class="fas fa-times"></i>
            </button>
          </div>
          <div class="modal-body">
            <p>Bn c chc chn mun khi phc tin tc "{{ selectedNews.title }}" khng?</p>
            <div class="form-actions">
              <button class="cancel-btn" @click="showRestoreModal = false">Hy</button>
              <button class="submit-btn" @click="handleRestore">Khi phc</button>
            </div>
          </div>
        </div>
      </div>

      <!-- Modal xc nhn xa vnh vin -->
      <div class="modal" v-if="showPermanentDeleteModal">
        <div class="modal-overlay" @click="showPermanentDeleteModal = false"></div>
        <div class="modal-content" @click.stop>
          <div class="modal-header">
            <h3>Xc nhn xa vnh vin</h3>
            <button class="close-btn" @click="showPermanentDeleteModal = false">
              <i class="fas fa-times"></i>
            </button>
          </div>
          <div class="modal-body">
            <p class="warning-text">Cnh bo: Hnh ng ny khng th hon tc!</p>
            <p>Bn c chc chn mun xa vnh vin tin tc "{{ selectedNews.title }}" khng?</p>
            <div class="form-actions">
              <button class="cancel-btn" @click="showPermanentDeleteModal = false">Hy</button>
              <button class="permanent-delete-btn" @click="handlePermanentDelete">
                Xa vnh vin
              </button>
            </div>
          </div>
        </div>
      </div>
    </template>
  </div>
</template>

<script>
import { ref, onMounted, computed, onBeforeUnmount } from 'vue'
import newsService from '@/api/services/newsService'
import eventBus from '@/eventBus'
import { baseMediaUrl } from '@/api/config'

// Import cc hnh nh t th mc assets
// const img1 = '@/assets/1746863140024.png'
// const img2 = '@/assets/1746678606025.png'
// const img3 = '@/assets/1746678511693.png'

export default {
  name: 'NewsList',
  setup() {
    // Khai bo cc bin phn ng (reactive)
    const news = ref([]) // D liu tin tc
    const searchQuery = ref('') // Chui tm kim
    const filterType = ref('') // Loi tin cn lc
    const showDetailsModal = ref(false) // iu khin hin th modal chi tit
    const showFormModal = ref(false) // iu khin hin th modal form
    const showSoftDeleteModal = ref(false) // iu khin hin th modal xa tm thi
    const showRestoreModal = ref(false) // iu khin hin th modal khi phc
    const showPermanentDeleteModal = ref(false) // iu khin hin th modal xa vnh vin
    const selectedNews = ref({}) // Tin tc c chn  thao tc
    const formData = ref({
      title: '',
      summary: '',
      content: '',
      image: null,
      type: '',
      isDeleted: false,
    }) // D liu form
    const imagePreview = ref(null) // URL xem trc nh
    const uploadProgress = ref(0) // Tin trnh ti nh
    const loading = ref(false) // Trng thi ang ti
    const error = ref(null) // Thng bo li nu c
    const isEditing = ref(false) // Trng thi ang chnh sa hay thm mi
    const baseImageUrl = ref(baseMediaUrl) // URL c s cho hnh nh
    const imageLoadError = ref({}) // Lu cc li khi ti hnh nh
    const selectedNewsIds = ref([]) // Danh sch ID tin tc c chn
    const fileInput = ref(null)
    const errors = ref({}) // Add a reactive object for errors
    
    // nh ngha mng nh mc nh
    // const defaultImages = [img1, img2, img3]

    // Tnh ton danh sch tin  lc
    const filteredNews = computed(() => {
      let filtered = [...news.value]

      // Lc b tin  xa
      filtered = filtered.filter((item) => !item.isDeleted)

      // Tm kim theo tiu 
      if (searchQuery.value) {
        const query = searchQuery.value.toLowerCase()
        filtered = filtered.filter((item) => item.title.toLowerCase().includes(query))
      }

      // Lc theo loi
      if (filterType.value) {
        filtered = filtered.filter((item) => item.type === filterType.value)
      }

      return filtered
    })

    // Chn tt c
    const isAllSelected = computed(
      () =>
        filteredNews.value.length > 0 &&
        filteredNews.value.every((news) => selectedNewsIds.value.includes(news._id))
    )
    const isSelected = (id) => selectedNewsIds.value.includes(id)
    const toggleSelect = (id) => {
      const idx = selectedNewsIds.value.indexOf(id)
      if (idx === -1) selectedNewsIds.value.push(id)
      else selectedNewsIds.value.splice(idx, 1)
    }
    const toggleSelectAll = () => {
      if (isAllSelected.value) selectedNewsIds.value = []
      else selectedNewsIds.value = filteredNews.value.map((news) => news._id)
    }

    // Ti danh sch tin tc t API
    const loadNews = async () => {
      loading.value = true
      error.value = null

      try {
        console.log('ang gi API getNews...')
        const response = await newsService.getNews()
        console.log('Kt qu API:', response)

        // Kim tra cu trc d liu tr v
        if (!response || !response.data) {
          throw new Error('D liu tr v khng hp l')
        }

        // S dng d liu t API trc tip, khng lc qua localStorage
        news.value = response.data
        console.log('Filtered news:', news.value)
      } catch (err) {
        console.error('Error loading news:', err)
        error.value = 'Khng th ti danh sch tin tc. Vui lng th li sau.'
      } finally {
        loading.value = false
      }
    }

    // Kim tra xem ngi dng c quyn chnh sa bi vit khng
    const canEditNews = (news) => {
      // Ly thng tin ngi dng t localStorage
      const adminInfo = JSON.parse(localStorage.getItem('adminInfo') || '{}')

      // Kim tra xem ngi dng c phi l admin khng (admin c th sa mi bi vit)
      if (adminInfo.role === 'admin') {
        return true
      }

      // Staff ch c th sa bi vit ca chnh mnh
      if (adminInfo.role === 'staff' && news.author === adminInfo.fullName) {
        return true
      }

      // Khng c quyn sa
      return false
    }

    // Kim tra xem ngi dng c quyn xa bi vit khng
    const canDeleteNews = (news) => {
      // X l tng t nh canEditNews
      return canEditNews(news)
    }

    // X l s kin tm kim
    const handleSearch = () => {
      // Thc hin tm kim thng qua computed property
    }

    // X l s kin lc
    const handleFilter = () => {
      // Thc hin lc thng qua computed property
    }

    // Hin th chi tit tin tc
    const showDetails = (item) => {
      selectedNews.value = item
      showDetailsModal.value = true
    }

    // M modal chnh sa tin tc
    const openEditModal = async (news) => {
      try {
        const newsDetail = await newsService.getNewsById(news._id)
        if (newsDetail) {
          selectedNews.value = newsDetail
          formData.value = {
            title: newsDetail.title || '',
            summary: newsDetail.summary || '',
            content: newsDetail.content || '',
            type: newsDetail.type || '',
            image: newsDetail.image || null,
            isDeleted: newsDetail.isDeleted || false,
          }

          // Cp nht x l hin th nh
          if (newsDetail.image) {
            const imageUrl = getImageUrl(newsDetail.image)
            if (imageUrl) {
              imagePreview.value = imageUrl
            } else {
              imagePreview.value = null
              console.error('Khng th to URL cho nh:', newsDetail.image)
            }
          } else {
            imagePreview.value = null
          }

          isEditing.value = true
          showFormModal.value = true
          // Khng cn hiu ng khi sa
          setTimeout(() => {
            const modal = document.querySelector('.modal-content')
            if (modal) modal.classList.add('no-animation')
          }, 0)
        }
      } catch (error) {
        console.error('Error fetching news details:', error)
        eventBus.emit('show-toast', {
          type: 'error',
          message: 'Khng th ti thng tin tin tc. Vui lng th li sau.',
        })
      }
    }

    // Hin th modal xc nhn xa tm thi
    const confirmSoftDelete = (item) => {
      selectedNews.value = item
      showSoftDeleteModal.value = true
    }

    // Hin th modal xc nhn khi phc
    const confirmRestore = (item) => {
      selectedNews.value = item
      showRestoreModal.value = true
    }

    // Hin th modal xc nhn xa vnh vin
    const confirmPermanentDelete = (item) => {
      selectedNews.value = item
      showPermanentDeleteModal.value = true
    }

    // X l xa tm thi tin tc (chuyn vo thng rc)
    const handleSoftDelete = async () => {
      try {
        // Gi API  cp nht trng thi isDeleted thnh true trong database
        await newsService.deleteNews(selectedNews.value._id)

        // Lm mi danh sch tin tc sau khi xa
        await loadNews()

        showSoftDeleteModal.value = false
        eventBus.emit('update-deleted-news-count')

        eventBus.emit('show-toast', {
          type: 'success',
          message: ' chuyn tin tc vo thng rc',
        })
      } catch (error) {
        console.error('Error:', error)
        eventBus.emit('show-toast', {
          type: 'error',
          message: 'C li xy ra khi chuyn tin tc vo thng rc',
        })
      }
    }

    // X l khi phc tin tc t thng rc
    const handleRestore = async () => {
      try {
        await newsService.restoreNews(selectedNews.value._id)
        await loadNews()
        showRestoreModal.value = false
      } catch (error) {
        console.error('Error:', error)
      }
    }

    // X l xa vnh vin tin tc
    const handlePermanentDelete = async () => {
      try {
        await newsService.permanentDeleteNews(selectedNews.value._id)
        await loadNews()
        showPermanentDeleteModal.value = false
      } catch (error) {
        console.error('Error:', error)
      }
    }

    // X l s kin ti nh ln
    const handleImageUpload = (event) => {
      const file = event.target.files[0]
      if (!file) return

      // Kim tra kch thc file (10MB limit)
      const maxFileSize = 10 * 1024 * 1024 // 10MB
      if (file.size > maxFileSize) {
        eventBus.emit('show-toast', {
          type: 'error',
          message: `Kch thc file khng c vt qu 10MB`,
        })
        event.target.value = ''
        return
      }

      // Kim tra loi file
      const allowedTypes = ['image/jpeg', 'image/png', 'image/gif']
      if (!allowedTypes.includes(file.type)) {
        eventBus.emit('show-toast', {
          type: 'error',
          message: 'Ch chp nhn file nh nh dng JPG, PNG hoc GIF',
        })
        event.target.value = ''
        return
      }

      // Kim tra kch thc nh
      const img = new Image()
      img.onload = () => {
        URL.revokeObjectURL(img.src)

        // Kim tra kch thc ti thiu
        if (img.width < 300 || img.height < 200) {
          eventBus.emit('show-toast', {
            type: 'error',
            message: 'Kch thc nh qu nh (ti thiu 300x200 pixels)',
          })
          event.target.value = ''
          formData.value.image = null
          imagePreview.value = null
          return
        }

        // Kim tra kch thc ti a
        if (img.width > 2000 || img.height > 2000) {
          eventBus.emit('show-toast', {
            type: 'error',
            message: 'Kch thc nh qu ln (ti a 2000x2000 pixels)',
          })
          event.target.value = ''
          formData.value.image = null
          imagePreview.value = null
          return
        }

        // Lu nh vo form data
        formData.value.image = file
        if (imagePreview.value) {
          URL.revokeObjectURL(imagePreview.value)
        }
        imagePreview.value = URL.createObjectURL(file)
      }

      img.onerror = () => {
        URL.revokeObjectURL(img.src)
        eventBus.emit('show-toast', {
          type: 'error',
          message: 'File khng phi l nh hp l',
        })
        event.target.value = ''
        formData.value.image = null
        imagePreview.value = null
      }

      img.src = URL.createObjectURL(file)
    }

    // Ly URL y  ca nh
    const getImageUrl = (imagePath) => {
      if (!imagePath) return `${baseImageUrl.value}/uploads/images/1746862099720.png?t=${new Date().getTime()}`

      // Nu  l URL y 
      if (imagePath.startsWith('http')) return imagePath

      try {
        // Ly tn file t ng dn
        const filename = imagePath.split('/').pop().split('\\').pop()
        
        // To ng dn tuyt i n backend vi timestamp  trnh cache
        return `${baseImageUrl.value}/uploads/images/${filename}?t=${new Date().getTime()}`
      } catch (error) {
        console.error('Error creating image URL:', error)
        return `${baseImageUrl.value}/uploads/images/1746862099720.png?t=${new Date().getTime()}`
      }
    }

    // nh dng hin th ngy thng
    const formatDate = (date) => {
      return new Date(date).toLocaleDateString('vi-VN')
    }

    // nh dng hin th loi tin tc
    const formatType = (type) => {
      const types = {
        'tin-tuc': 'Tin tc',
        'su-kien': 'S kin',
        'thong-bao': 'Thng bo',
      }
      return types[type] || type
    }

    // Rt gn ni dung m t  hin th
    const formatDescription = (text) => {
      return text?.length > 50 ? text.slice(0, 50) + '...' : text
    }

    // M modal thm tin tc mi
    const openAddModal = () => {
      isEditing.value = false
      formData.value = {
        title: '',
        summary: '',
        content: '',
        image: null,
        type: '',
        view: 0,
        like: 0,
        isDeleted: false,
      }
      imagePreview.value = null
      uploadProgress.value = 0
      showFormModal.value = true
    }

    // Validation method
    const validateForm = () => {
      const newErrors = {}

      // Validate title
      if (!formData.value.title?.trim()) {
        newErrors.title = 'Tiu  khng c  trng'
      } else if (formData.value.title.trim().length < 3) {
        newErrors.title = 'Tiu  phi c t nht 3 k t'
      } else if (formData.value.title.trim().length > 200) {
        newErrors.title = 'Tiu  khng c vt qu 200 k t'
      }

      // Validate summary
      if (!formData.value.summary?.trim()) {
        newErrors.summary = 'Tm tt khng c  trng'
      } else if (formData.value.summary.trim().length < 20) {
        newErrors.summary = 'Tm tt phi c t nht 20 k t'
      } else if (formData.value.summary.trim().length > 500) {
        newErrors.summary = 'Tm tt khng c vt qu 500 k t'
      }

      // Validate content
      if (!formData.value.content?.trim()) {
        newErrors.content = 'Ni dung khng c  trng'
      } else if (formData.value.content.trim().length < 100) {
        newErrors.content = 'Ni dung phi c t nht 100 k t'
      } else if (formData.value.content.trim().length > 5000) {
        newErrors.content = 'Ni dung khng c vt qu 5000 k t'
      }

      // Validate type
      if (!formData.value.type?.trim()) {
        newErrors.type = 'Phn loi khng c  trng'
      } else if (formData.value.type.trim().length > 50) {
        newErrors.type = 'Phn loi khng c vt qu 50 k t'
      }

      // Validate image if necessary
      if (!formData.value.image && !imagePreview.value) {
        newErrors.image = 'Vui lng chn nh cho tin tc'
      }

      errors.value = newErrors // Update the errors reactive object
      return Object.keys(newErrors).length === 0 // Return true if no errors
    }

    // Update handleSubmit to include validation
    const handleSubmit = async () => {
      if (!validateForm()) {
        // Focus on the first error field
        const firstErrorField = Object.keys(errors.value)[0]
        const element = document.querySelector(`[name="${firstErrorField}"]`)
        if (element) {
          element.focus()
        }
        return
      }

      try {
        // Chun b d liu  gi ln server
        const newsData = {
          title: (formData.value.title || '').trim(),
          summary: (formData.value.summary || '').trim(),
          content: (formData.value.content || '').trim(),
          type: (formData.value.type || '').trim(),
          // Khng gi publishedDate nu khng c gi tr hp l
          // Khng gi cc trng khng cn thit
        }

        // X l nh
        if (formData.value.image instanceof File) {
          const imageFormData = new FormData()
          imageFormData.append('image', formData.value.image)

          try {
            const uploadResponse = await newsService.uploadImage(imageFormData)
            if (uploadResponse && uploadResponse.imagePath) {
              newsData.image = uploadResponse.imagePath
            } else {
              throw new Error('Khng nhn c ng dn nh sau khi upload')
            }
          } catch (uploadError) {
            console.error('Error uploading image:', uploadError)
            eventBus.emit('show-toast', {
              type: 'error',
              message: 'Li khi ti nh ln. Vui lng th li.',
            })
            return
          }
        } else if (typeof formData.value.image === 'string') {
          newsData.image = formData.value.image
        }

        // Thm author nu backend yu cu (ly t localStorage hoc mc nh)
        const adminInfo = JSON.parse(localStorage.getItem('adminInfo') || '{}')
        newsData.author = adminInfo.fullName || 'Admin'

        // Gi d liu ln server
        if (isEditing.value) {
          await newsService.updateNews(selectedNews.value._id, newsData)
          eventBus.emit('show-toast', {
            type: 'success',
            message: 'Cp nht tin tc thnh cng!',
            icon: 'fas fa-check-circle',
            duration: 3000,
          })
        } else {
          await newsService.createNews(newsData)
          eventBus.emit('show-toast', {
            type: 'success',
            message: 'To tin tc mi thnh cng!',
            icon: 'fas fa-check-circle',
            duration: 3000,
          })
        }

        await loadNews()
        showFormModal.value = false
        imagePreview.value = null
        uploadProgress.value = 0
      } catch (err) {
        console.error('Error:', err)
        eventBus.emit('show-toast', {
          type: 'error',
          message: isEditing.value
            ? 'Khng th cp nht tin tc. Vui lng th li sau.'
            : 'Khng th to tin tc mi. Vui lng th li sau.',
        })
      }
    }

    // X l xa tin tc
    const handleDelete = async () => {
      try {
        await newsService.deleteNews(selectedNews.value._id)
        await loadNews()
        showSoftDeleteModal.value = false
      } catch (err) {
        console.error('Error:', err)
        error.value = 'Khng th xa tin tc. Vui lng th li sau.'
      }
    }

    // X l li khi ti hnh nh
    const handleImageError = (event, newsId) => {
      if (event) {
        // Thay th bng nh mc nh vi timestamp  trnh cache
        event.target.src = `${baseImageUrl.value}/uploads/images/1746862099720.png?t=${new Date().getTime()}`

        // Ghi nh li cho newsId ny
        imageLoadError.value[newsId] = true
        console.error('Failed to load image for news ID:', newsId)
      }
    }

    const openEditFromDetails = () => {
      if (!canEditNews(selectedNews.value)) {
        showPermissionError('sa')
        return
      }
      showDetailsModal.value = false
      openEditModal(selectedNews.value)
    }

    // Khi ng modal form, b class no-animation  ln sau thm mi c hiu ng
    const closeFormModal = () => {
      showFormModal.value = false
      setTimeout(() => {
        const modal = document.querySelector('.modal-content')
        if (modal) modal.classList.remove('no-animation')
      }, 300)
    }

    // Thm hm xa nh
    const removeImage = () => {
      imagePreview.value = null
      formData.value.image = null
      if (fileInput.value) {
        fileInput.value.value = ''
      }
    }

    // Thm hm trigger  m dialog chn file
    const triggerFileInput = () => {
      if (fileInput.value) {
        fileInput.value.click()
      }
    }

    // Thm hm hin th thng bo li quyn
    const showPermissionError = (action) => {
      eventBus.emit('show-toast', {
        type: 'error',
        message: `Bn khng c quyn ${action} bi vit ny`,
      })
    }

    // Gi khi component c to
    onMounted(() => {
      loadNews()
      // Lng nghe s kin cp nht t TrashNews
      eventBus.on('update-deleted-news-count', () => {
        loadNews()
      })
      console.log('Base Image URL:', baseImageUrl.value)
    })

    // Dn dp khi component b hy
    onBeforeUnmount(() => {
      // Dn dp cc event listener
      eventBus.off('update-deleted-news-count')
      // Dn dp cc URL xem trc nh
      if (imagePreview.value) {
        URL.revokeObjectURL(imagePreview.value)
      }
    })

    // Tr v cc bin v phng thc s dng trong template
    return {
      news,
      searchQuery,
      filterType,
      showDetailsModal,
      showFormModal,
      showSoftDeleteModal,
      showRestoreModal,
      showPermanentDeleteModal,
      selectedNews,
      formData,
      imagePreview,
      uploadProgress,
      loading,
      error,
      isEditing,
      filteredNews,
      handleSearch,
      handleFilter,
      showDetails,
      openEditModal,
      confirmSoftDelete,
      confirmRestore,
      confirmPermanentDelete,
      handleSoftDelete,
      handleRestore,
      handlePermanentDelete,
      handleImageUpload,
      getImageUrl,
      formatDate,
      formatType,
      formatDescription,
      openAddModal,
      handleSubmit,
      handleDelete,
      imageLoadError,
      handleImageError,
      canEditNews,
      canDeleteNews,
      showPermissionError,
      openEditFromDetails,
      selectedNewsIds,
      isAllSelected,
      isSelected,
      toggleSelect,
      toggleSelectAll,
      closeFormModal,
      removeImage,
      fileInput,
      triggerFileInput,
      errors,
      validateForm,
    }
  },
}
</script>

