.modal-header {
  background: linear-gradient(to right, #f8fafc, #f1f5f9);
  border-bottom: 1px solid #e2e8f0;
  padding: 1.5rem 2rem;
  display: flex;
  align-items: center;
  justify-content: space-between; /* a cc phn t v hai pha */
}

.modal-header h3 {
  font-size: 1.5rem;
  background: linear-gradient(to right, #1e293b, #334155);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  font-weight: 700;
  margin: 0;
  letter-spacing: -0.025em;
}

.close-btn {
  background: white;
  border: 1px solid #e2e8f0;
  color: #64748b;
  width: 36px;
  height: 36px;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  transition: all 0.2s ease;
  margin-left: auto; /* y nt ng sang phi */
}

.close-btn:hover {
  background: #ef4444;
  color: white;
  border-color: #ef4444;
  transform: rotate(90deg);
}

.modal-body {
  padding: 20px;
  background: white;
}

/* Animation Enhancements */
.modal {
  animation: modalFadeIn 0.4s cubic-bezier(0.4, 0, 0.2, 1);
  backdrop-filter: blur(4px);
}

.modal-content {
  animation: modalSlideIn 0.4s cubic-bezier(0.4, 0, 0.2, 1);
  transform-origin: top;
}

/* Khng hiu ng khi l modal form chnh sa */
.modal-content.no-animation {
  animation: none !important;
}

@keyframes modalFadeIn {
  from {
    opacity: 0;
    backdrop-filter: blur(0);
  }
  to {
    opacity: 1;
    backdrop-filter: blur(4px);
  }
}

@keyframes modalSlideIn {
  from {
    transform: translateY(-50px) scale(0.95);
    opacity: 0;
  }
  to {
    transform: translateY(0) scale(1);
    opacity: 1;
  }
}

/* Content Formatting */
.detail-item p.truncate-text {
  display: -webkit-box;
  -webkit-line-clamp: 3;
  -webkit-box-orient: vertical;
  overflow: hidden;
  text-overflow: ellipsis;
}

/* Responsive Modal Content */
@media (max-width: 640px) {
  .detail-item label {
    font-size: 0.875rem;
  }

  .detail-item p {
    font-size: 0.9375rem;
  }

  .detail-image {
    max-width: 100%;
  }

  .modal-header h3 {
    font-size: 1.25rem;
  }
}

/* Modal Animation */
.modal {
  animation: modalFadeIn 0.3s ease;
}

.modal-content {
  animation: modalSlideIn 0.3s ease;
}

@keyframes modalFadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

@keyframes modalSlideIn {
  from {
    transform: translateY(-20px);
    opacity: 0;
  }
  to {
    transform: translateY(0);
    opacity: 1;
  }
}

th input[type='checkbox'],
td input[type='checkbox'] {
  display: block;
  margin: 0 auto;
  width: 18px;
  height: 18px;
}

.upload-placeholder {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  color: #2563eb;
  background: #f8fbff;
  border-radius: 8px;
  padding: 32px 0 20px 0;
  cursor: pointer;
  min-height: 120px;
}

.upload-icon {
  font-size: 38px;
  color: #3b82f6;
  margin-bottom: 8px;
}

.upload-text {
  font-size: 18px;
  font-weight: 500;
  color: #222;
  margin-bottom: 4px;
}

.upload-subtext {
  font-size: 13px;
  color: #64748b;
}

.upload-subtext span {
  color: #2563eb;
  font-weight: 500;
}
</style>
