import api from '../config'

// i tng bookingService cha cc phng thc  tng tc vi API t lch
export const bookingService = {
  // Ly tt c cc t lch
  getAllBookings: async () => {
    try {
      const response = await api.get('/bookings') // Gi yu cu GET n API  ly danh sch t lch
      return response.data // Tr v d liu t phn hi
    } catch (error) {
      console.error('Li khi ly danh sch t lch:', error) // In ra li nu c
      throw error // Nm li ra ngoi  x l
    }
  },

  // Ly t lch theo ID
  getBookingById: async (id) => {
    try {
      const response = await api.get(`/bookings/${id}`) // Gi yu cu GET n API vi ID c th
      return response.data // Tr v d liu t phn hi
    } catch (error) {
      console.error('Li khi ly thng tin t lch:', error) // In ra li nu c
      throw error // Nm li ra ngoi  x l
    }
  },

  // Cp nht trng thi t lch
  updateBookingStatus: async (id, status) => {
    try {
      const response = await api.put(`/bookings/${id}`, { status }) // Gi yu cu PUT  cp nht trng thi t lch
      return response.data // Tr v d liu t phn hi
    } catch (error) {
      console.error('Li khi cp nht trng thi t lch:', error) // In ra li nu c
      throw error // Nm li ra ngoi  x l
    }
  },
  
  // To t lch mi
  createBooking: async (bookingData) => {
    try {
      const response = await api.post('/bookings', bookingData) // Gi yu cu POST  to t lch mi
      return response.data // Tr v d liu t phn hi
    } catch (error) {
      console.error('Li khi to t lch:', error) // In ra li nu c
      throw error // Nm li ra ngoi  x l
    }
  },
}

export default bookingService // Xut i tng bookingService  s dng  ni khc
