<template>
  <div class="admin-container">
    <div class="account-list">
      <div class="actions-header">
        <div class="search-section">
          <div class="filter-box">
            <select v-model="statusFilter">
              <option value="all">Tt c trng thi</option>
              <option value="completed"> hon thnh</option>
              <option value="cancelled"> hy</option>
            </select>
          </div>
          <div class="search-box">
            <input
              v-model="search"
              type="text"
              placeholder="Tm kim theo tn, email, s in thoi, dch v..."
            />
            <i class="fas fa-search"></i>
          </div>
        </div>
        <div class="right-section">
          <router-link to="/admin/dat-lich/cho-xu-ly" class="pending-btn">
            <i class="fas fa-clock"></i>
            t lch ch x l
          </router-link>
        </div>
      </div>

      <div v-if="loading" class="loading-container">
        <!-- <div class="loading-spinner"></div> -->
        <div>ang ti d liu...</div>
      </div>
      <div v-else-if="error" class="error-container">
        <div class="error-message">{{ error }}</div>
        <button class="retry-btn" @click="fetchBookings">Th li</button>
      </div>
      <div v-else-if="filteredBookings.length === 0" class="empty-state">
        <i class="fas fa-calendar-times"></i>
        <span>Khng c t lch no ph hp</span>
      </div>
      <div v-else>
        <div class="table-container">
          <table>
            <thead>
              <tr>
                <th>No.</th>
                <th>H tn</th>
                <!-- <th>Email</th> -->
                <!-- <th>S in thoi</th> -->
                <th>Dch v</th>
                <th>Trng thi</th>
                <th>Ngy t</th>
                <th>Thao tc</th>
              </tr>
            </thead>
            <tbody>
              <tr v-for="(booking, index) in filteredBookings" :key="booking._id">
                <td>{{ index + 1 }}</td>
                <td>{{ booking.fullName }}</td>
                <!-- <td>{{ booking.email }}</td> -->
                <!-- <td>{{ booking.phone }}</td> -->
                <td>{{ booking.service }}</td>
                <td>
                  <span :class="['status-badge', booking.status]">
                    {{ getStatusText(booking.status) }}
                  </span>
                </td>
                <td>{{ formatDate(booking.createdAt) }}</td>
                <td class="actions-cell">
                  <div class="actions">
                    <button
                      @click="viewDetails(booking)"
                      class="icon-btn info"
                      title="Xem chi tit"
                    >
                      <i class="fas fa-info-circle"></i>
                    </button>
                  </div>
                </td>
              </tr>
            </tbody>
          </table>
        </div>
      </div>

      <!-- Modal chi tit -->
      <div v-if="selectedBooking" class="modal">
        <div class="modal-overlay" @click="selectedBooking = null"></div>
        <div class="modal-content" @click.stop>
          <div class="modal-header">
            <h3>Chi tit t lch</h3>
            <button @click="selectedBooking = null" class="close-btn">
              <i class="fas fa-times"></i>
            </button>
          </div>
          <div class="modal-body">
            <div class="detail-item">
              <label>H tn:</label>
              <p>{{ selectedBooking.fullName }}</p>
            </div>
            <div class="detail-item">
              <label>Email:</label>
              <p>{{ selectedBooking.email }}</p>
            </div>
            <div class="detail-item">
              <label>Dch v:</label>
              <p>{{ selectedBooking.service }}</p>
            </div>
            <div class="detail-item">
              <label>Ghi ch:</label>
              <p>{{ selectedBooking.note || 'Khng c' }}</p>
            </div>
            <div class="detail-item">
              <label>Trng thi:</label>
              <span :class="['status-badge', selectedBooking.status]">
                {{ getStatusText(selectedBooking.status) }}
              </span>
            </div>
            <div class="detail-item">
              <label>Ngy t:</label>
              <p>{{ formatDate(selectedBooking.createdAt) }}</p>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { bookingService } from '@/api/services/bookingService'

export default {
  name: 'BookingList',
  data() {
    return {
      bookings: [], // Danh sch t lch
      selectedBooking: null, // t lch c chn  xem chi tit
      loading: false, // Trng thi ti d liu
      error: null, // Thng bo li nu c
      search: '', // Gi tr tm kim
      statusFilter: 'all', // B lc trng thi
    }
  },
  computed: {
    filteredBookings() {
      let filtered = this.bookings.filter(
        (b) => b.status === 'completed' || b.status === 'cancelled' // Lc cc t lch  hon thnh hoc  hy
      )
      if (this.statusFilter !== 'all') {
        filtered = filtered.filter((b) => b.status === this.statusFilter) // Lc theo trng thi nu khng phi 'all'
      }
      if (this.search) {
        const s = this.search.toLowerCase() // Chuyn gi tr tm kim thnh ch thng
        filtered = filtered.filter(
          (b) =>
            (b.fullName && b.fullName.toLowerCase().includes(s)) || // Lc theo h tn
            (b.email && b.email.toLowerCase().includes(s)) || // Lc theo email
            (b.phone && b.phone.toLowerCase().includes(s)) || // Lc theo s in thoi
            (b.service && b.service.toLowerCase().includes(s)) // Lc theo dch v
        )
      }
      return filtered // Tr v danh sch  lc
    },
  },
  methods: {
    async fetchBookings() {
      try {
        this.loading = true // Bt u ti d liu
        this.error = null // t li thng bo li
        const response = await bookingService.getAllBookings() // Gi dch v  ly danh sch t lch
        this.bookings = response.data || [] // Cp nht danh sch t lch
      } catch (error) {
        console.error('Error fetching bookings:', error) // Ghi log li
        this.error = 'Khng th ti danh sch t lch. Vui lng th li sau.' // Cp nht thng bo li
      } finally {
        this.loading = false // Kt thc qu trnh ti d liu
      }
    },
    viewDetails(booking) {
      this.selectedBooking = booking // t lch c chn  xem chi tit
    },
    getStatusText(status) {
      const statusMap = {
        processing: 'ang x l', // Trng thi ang x l
        completed: ' hon thnh', // Trng thi  hon thnh
        cancelled: ' hy', // Trng thi  hy
      }
      return statusMap[status] || status // Tr v vn bn trng thi tng ng
    },
    formatDate(date) {
      const dateObj = new Date(date) // Chuyn i chui ngy thnh i tng Date
      const dateString = dateObj.toLocaleDateString('vi-VN', {
        day: '2-digit',
        month: '2-digit',
        year: 'numeric',
      }) // nh dng ngy
      const timeString = dateObj.toLocaleTimeString('vi-VN', {
        hour: '2-digit',
        minute: '2-digit',
      }) // nh dng gi
      return `${dateString} - ${timeString}` // Tr v chui ngy v gi
    },
  },
  created() {
    this.fetchBookings() // Gi hm  ti danh sch t lch khi component c to
  },
}
</script>

