<template>
  <!-- Thng rc cha cc tin tc  xa tm thi -->
  <div class="trash-news">
    <!-- Phn header vi cng c tm kim v cc nt hnh ng hng lot -->
    <div class="header-actions">
      <div class="actions-header">
        <!-- Khu vc tm kim v lc -->
        <div class="search-filter">
          <div class="search-box">
            <i class="fas fa-search"></i>
            <input
              type="text"
              v-model="searchQuery"
              placeholder="Tm kim theo tiu ..."
              @input="handleSearch"
            />
          </div>
        </div>

        <!-- Nt khi phc hng lot - ch hin th khi c mc c chn -->
        <button
          v-if="selectedNews.length > 0"
          class="bulk-action-btn restore"
          @click="confirmBulkRestore"
          :disabled="loading"
        >
          <i v-if="loading" class="fas fa-spinner fa-spin"></i>
          <i v-else class="fas fa-rotate-left"></i>
          {{ loading ? 'ang x l...' : 'Khi phc  chn' }}
        </button>

        <!-- Nt xa vnh vin hng lot - ch hin th khi c mc c chn -->
        <button
          v-if="selectedNews.length > 0"
          class="bulk-action-btn delete"
          @click="confirmBulkDelete"
        >
          <i class="fas fa-trash"></i>
          Xa vnh vin  chn
        </button>
      </div>
    </div>

    <!-- Bng danh sch tin tc trong thng rc -->
    <div class="table-container">
      <table>
        <thead>
          <tr>
            <!-- Ct checkbox chn tt c -->
            <th width="50px">
              <input type="checkbox" :checked="isAllSelected" @change="toggleSelectAll" />
            </th>
            <th>No.</th>
            <th>nh</th>
            <th>Tiu </th>
            <th>Loi</th>
            <th>Tc gi</th>
            <th>Ngy xa</th>
            <th class="action-column">Thao tc</th>
          </tr>
        </thead>
        <tbody>
          <!-- Hin th tng dng tin tc -->
          <tr v-for="(news, index) in filteredNews" :key="news._id">
            <!-- Checkbox chn tng dng -->
            <td>
              <input
                type="checkbox"
                :checked="isSelected(news._id)"
                @change="toggleSelect(news._id)"
              />
            </td>
            <td>{{ index + 1 }}</td>
            <!-- nh i din tin tc -->
            <td>
              <div class="image-container">
                <img
                  :src="news.imageUrl || getImageUrl(news.image)"
                  alt="News image"
                  class="news-image"
                  @error="handleImageError"
                />
              </div>
            </td>
            <td>{{ news.title }}</td>
            <td>{{ formatType(news.type) }}</td>
            <td>{{ news.author }}</td>
            <td>{{ currentDate }}</td>
            <!-- Cc nt thao tc cho tng dng -->
            <td>
              <div class="actions">
                <button class="icon-btn restore" @click="confirmRestore(news)" title="Khi phc" :disabled="loading">
                  <i v-if="loading && selectedNews.includes(news._id)" class="fas fa-spinner fa-spin"></i>
                  <i v-else class="fas fa-rotate-left"></i>
                </button>
                <button class="icon-btn delete" @click="confirmDelete(news)" title="Xa vnh vin" :disabled="loading">
                  <i class="fas fa-trash"></i>
                </button>
              </div>
            </td>
          </tr>
          <!-- Hin th thng bo khi khng c d liu -->
          <tr v-if="filteredNews.length === 0">
            <td colspan="8" class="empty-message">Khng c tin tc no trong thng rc</td>
          </tr>
        </tbody>
      </table>
    </div>

    <!-- Modal xc nhn khi phc tin tc -->
    <div class="modal" v-if="showRestoreModal">
      <div class="modal-overlay" @click="showRestoreModal = false"></div>
      <div class="modal-content" @click.stop>
        <div class="modal-header">
          <h3>Xc nhn khi phc</h3>
          <button class="close-btn" @click="showRestoreModal = false">
            <i class="fas fa-times"></i>
          </button>
        </div>
        <div class="modal-body">
          <p>
            Bn c chc chn mun khi phc {{ selectedNews.length > 1 ? 'cc' : '' }} tin tc 
            chn khng?
          </p>
          <div class="form-actions">
            <button class="cancel-btn" @click="showRestoreModal = false" :disabled="loading">Hy</button>
            <button class="submit-btn" @click="handleRestore" :disabled="loading">
              <i v-if="loading" class="fas fa-spinner fa-spin"></i>
              {{ loading ? 'ang x l...' : 'Khi phc' }}
            </button>
          </div>
        </div>
      </div>
    </div>

    <!-- Modal xc nhn xa vnh vin tin tc -->
    <div class="modal" v-if="showDeleteModal">
      <div class="modal-overlay" @click="showDeleteModal = false"></div>
      <div class="modal-content" @click.stop>
        <div class="modal-header">
          <h3>Xc nhn xa vnh vin</h3>
          <button class="close-btn" @click="showDeleteModal = false">
            <i class="fas fa-times"></i>
          </button>
        </div>
        <div class="modal-body">
          <p class="warning-text">Cnh bo: Hnh ng ny khng th hon tc!</p>
          <p>
            Bn c chc chn mun xa vnh vin {{ selectedNews.length > 1 ? 'cc' : '' }} tin tc
             chn khng?
          </p>
          <div class="form-actions">
            <button class="cancel-btn" @click="showDeleteModal = false">Hy</button>
            <button class="delete-btn" @click="handleDelete">Xa vnh vin</button>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import eventBus from '@/eventBus'
import newsService from '@/api/services/newsService'
import { baseMediaUrl } from '@/api/config'

// Import cc hnh nh t th mc assets
import img1 from '@/assets/1746863140024.png'
import img2 from '@/assets/1746678606025.png'
import img3 from '@/assets/1746678511693.png'

export default {
  name: 'AdminTrashNews',
  data() {
    return {
      news: [], // Danh sch tin tc  xa
      searchQuery: '', // Chui tm kim
      selectedNews: [], // Danh sch ID tin tc  chn
      showRestoreModal: false, // iu khin hin th modal khi phc
      showDeleteModal: false, // iu khin hin th modal xa
      loading: false, // Trng thi ang ti
      error: null, // Lu thng tin li nu c
      baseImageUrl: baseMediaUrl, // URL c s  hin th hnh nh
      filterType: '', // Lc theo loi tin tc
    }
  },
  computed: {
    // Lc tin tc da trn iu kin tm kim v lc
    filteredNews() {
      // Danh sch tin  c lc t API
      let result = this.news

      // p dng b lc tm kim theo text
      if (this.searchQuery) {
        const query = this.searchQuery.toLowerCase()
        result = result.filter((news) => news.title.toLowerCase().includes(query))
      }

      // p dng b lc theo loi tin tc
      if (this.filterType) {
        result = result.filter((news) => news.type === this.filterType)
      }

      return result
    },
    // Kim tra xem tt c cc mc c ang c chn khng
    isAllSelected() {
      return (
        this.filteredNews.length > 0 &&
        this.filteredNews.every((news) => this.selectedNews.includes(news._id))
      )
    },
    // New computed property to get the current date
    currentDate() {
      const d = new Date() // Get the current date
      const day = d.getDate().toString().padStart(2, '0') // Get day and pad with zero if needed
      const month = (d.getMonth() + 1).toString().padStart(2, '0') // Get month (0-indexed) and pad
      const year = d.getFullYear() // Get full year
      const hour = d.getHours().toString().padStart(2, '0')
      const minute = d.getMinutes().toString().padStart(2, '0')
      return `${day}/${month}/${year} - ${hour}:${minute}`
    },
  },
  methods: {
    // Ti danh sch tin tc  xa t localStorage
    async loadNews() {
      try {
        // Ly danh sch tin t API thay v localStorage
        const response = await newsService.getNewsInTrash()
        if (response && response.data) {
          this.news = response.data
        } else {
          this.news = []
        }
      } catch (error) {
        console.error('Error loading news:', error)
        eventBus.emit('show-toast', {
          type: 'error',
          message: 'Khng th ti danh sch tin tc  xa',
        })
        this.news = []
      }
    },
    // X l s kin tm kim
    handleSearch() {
      // Trin khai debounce nu cn
    },
    // X l s kin lc
    handleFilter() {
      // Vic lc c x l t ng qua computed property
    },
    // nh dng hin th loi tin tc
    formatType(type) {
      const types = {
        'tin-tuc': 'Tin tc',
        'su-kien': 'S kin',
        'thong-bao': 'Thng bo',
      }
      return types[type] || type
    },
    // nh dng hin th ngy thng
    formatDate(date) {
      return new Date(date).toLocaleDateString('vi-VN', {
        year: 'numeric',
        month: 'long',
        day: 'numeric',
        hour: '2-digit',
        minute: '2-digit',
      })
    },
    // Ly ng dn y  ca hnh nh
    getImageUrl(imagePath) {
      if (!imagePath) return `${this.baseImageUrl}/uploads/images/1746862099720.png?t=${new Date().getTime()}`
      
      // Nu  l URL y , s dng trc tip vi timestamp
      if (imagePath.startsWith('http')) return `${imagePath}?t=${new Date().getTime()}`

      // Ly tn file t ng dn
      const filename = imagePath.split('/').pop().split('\\').pop()

      // To ng dn tuyt i n backend vi timestamp  trnh cache
      return `${this.baseImageUrl}/uploads/images/${filename}?t=${new Date().getTime()}`
    },
    // X l li khi ti hnh nh
    handleImageError(event) {
      if (event && event.target) {
        // Kim tra xem  x l li cho nh ny cha
        if (event.target.getAttribute('data-error-handled')) {
          return; // Ngn x l li lp li
        }
        
        // nh du  x l li
        event.target.setAttribute('data-error-handled', 'true');
        
        // S dng baseImageUrl thay v hardcode IP v thm timestamp  trnh cache
        event.target.src = `${this.baseImageUrl}/uploads/images/1746862099720.png?t=${new Date().getTime()}`
      }
    },
    // Tr v mt trong ba nh c nh da trn index
    getFixedImage(index) {
      const fixedImages = [img1, img2, img3]
      return fixedImages[index % fixedImages.length]
    },
    // Kim tra xem mt tin tc c ang c chn khng
    isSelected(id) {
      return this.selectedNews.includes(id)
    },
    // Bt/tt trng thi chn ca mt tin tc
    toggleSelect(id) {
      const index = this.selectedNews.indexOf(id)
      if (index === -1) {
        this.selectedNews.push(id)
      } else {
        this.selectedNews.splice(index, 1)
      }
    },
    // Bt/tt trng thi chn tt c cc tin tc
    toggleSelectAll() {
      if (this.isAllSelected) {
        this.selectedNews = []
      } else {
        this.selectedNews = this.filteredNews.map((news) => news._id)
      }
    },
    // Hin th modal xc nhn khi phc mt tin tc
    confirmRestore(news) {
      if (news) {
        this.selectedNews = [news._id]
      }
      this.showRestoreModal = true
    },
    // Hin th modal xc nhn xa mt tin tc
    confirmDelete(news) {
      if (news) {
        this.selectedNews = [news._id]
      }
      this.showDeleteModal = true
    },
    // Hin th modal xc nhn khi phc nhiu tin tc
    confirmBulkRestore() {
      this.showRestoreModal = true
    },
    // Hin th modal xc nhn xa nhiu tin tc
    confirmBulkDelete() {
      this.showDeleteModal = true
    },
    // X l khi phc tin tc
    async handleRestore() {
      try {
        this.loading = true;
        // Gi API  khi phc cc tin tc  chn
        for (const id of this.selectedNews) {
          try {
            await newsService.restoreNews(id);
          } catch (err) {
            console.error(`Li khi phc tin tc ID ${id}:`, err);
            throw err; // Nm li  x l  catch bn ngoi
          }
        }

        // Cp nht danh sch
        await this.loadNews();

        // Reset selection
        this.selectedNews = [];
        this.showRestoreModal = false;
        this.loading = false;

        // Thng bo thnh cng
        eventBus.emit('show-toast', {
          type: 'success',
          message: 'Khi phc tin tc thnh cng',
        });
        eventBus.emit('update-deleted-news-count');
      } catch (error) {
        this.loading = false;
        console.error('Li khi phc tin tc:', error);
        // Hin th thng bo li c th t server nu c
        const errorMessage = error.response?.data?.message || 'C li xy ra khi khi phc tin tc';
        eventBus.emit('show-toast', {
          type: 'error',
          message: errorMessage,
        });
      }
    },
    // X l xa vnh vin tin tc
    async handleDelete() {
      try {
        // Gi API xa vnh vin tng tin tc  chn
        for (const id of this.selectedNews) {
          try {
            await newsService.permanentDeleteNews(id)
          } catch (err) {
            // Nu c li khi xa tng tin, vn tip tc xa cc tin khc
            console.error('Error deleting news:', err)
          }
        }

        // Cp nht danh sch
        await this.loadNews()

        // Reset selection v ng modal
        this.selectedNews = []
        this.showDeleteModal = false

        // Thng bo thnh cng
        eventBus.emit('show-toast', {
          type: 'success',
          message: 'Xa vnh vin tin tc thnh cng',
        })
        eventBus.emit('update-deleted-news-count')
      } catch (error) {
        console.error('Error deleting news:', error)
        eventBus.emit('show-toast', {
          type: 'error',
          message: 'C li xy ra khi xa tin tc',
        })
      }
    },
    // Rt gn ID  hin th
    truncateId(id) {
      if (!id) return ''
      // Hin th 25 k t u tin ca ID, v ct c kch thc ln hn
      return id.length > 25 ? id.substring(0, 25) + '...' : id
    },
  },
  // Khi to d liu khi component c to
  created() {
    this.loadNews()
  },
  // Dn dp khi component b hy
  beforeUnmount() {
    // Cleanup if needed
  },
}
</script>

<style scoped>
