import api from '../config'

// i tng newsService cha cc phng thc  tng tc vi API tin tc
const newsService = {
  // Ly danh sch tin tc
  async getNews() {
    try {
      const response = await api.get('/news') // Gi yu cu GET n API  ly danh sch tin tc
      return {
        data: response.data.data || [], // Tr v d liu tin tc, nu khng c th tr v mng rng
                                       // Mi item trong data s c thuc tnh imageUrl c thm vo t API
        pagination: response.data.pagination || null, // Tr v thng tin phn trang, nu khng c th tr v null
        message: response.data.message || '', // Tr v thng ip, nu khng c th tr v chui rng
      }
    } catch (error) {
      console.error('Li getNews:', error) // Gi li log li c bn
      throw error // Nm li ra ngoi  x l
    }
  },

  // Ly chi tit tin tc
  async getNewsById(id) {
    try {
      // S dng cng cch gi API nh getNews  m bo nht qun
      const response = await api.get(`/news/${id}`)
      // data.data s c thuc tnh imageUrl c to t API
      return response.data.data
    } catch (error) {
      console.error('Li getNewsById:', error)
      
      // Nu li 401, th ly d liu thng qua API listing v lc theo ID
      if (error.response && error.response.status === 401) {
        try {
          // Ly danh sch tin tc v tm bi vit c th theo ID
          const newsListResponse = await api.get('/news')
          const newsList = newsListResponse.data.data || []
          const newsItem = newsList.find(item => (item._id || item.id) == id)
          
          if (newsItem) {
            return newsItem
          } else {
            console.error('Khng tm thy tin tc vi id:', id)
          }
        } catch (listError) {
          console.error('Li khi ly tin tc t danh sch:', listError)
        }
      }
      
      throw error
    }
  },

  // To tin tc mi
  async createNews(data) {
    try {
      const response = await api.post('/news', JSON.stringify(data), { // Gi yu cu POST  to tin tc mi
        headers: {
          'Content-Type': 'application/json', // t header cho yu cu
        },
      })
      return response.data // Tr v d liu t phn hi
    } catch (error) {
      console.error('Li to tin tc:', error) // In ra li nu c
      throw error // Nm li ra ngoi  x l
    }
  },

  // Cp nht tin tc
  async updateNews(id, data) {
    try {
      const response = await api.put(`/news/${id}`, JSON.stringify(data), { // Gi yu cu PUT  cp nht tin tc
        headers: {
          'Content-Type': 'application/json', // t header cho yu cu
        },
      })
      return response.data // Tr v d liu t phn hi
    } catch (error) {
      console.error('Li cp nht tin tc:', error) // In ra li nu c
      throw error // Nm li ra ngoi  x l
    }
  },

  // Xa tin tc
  async deleteNews(id) {
    try {
      const response = await api.delete(`/news/${id}`) // Gi yu cu DELETE  xa tin tc
      return response.data // Tr v d liu t phn hi
    } catch (error) {
      console.error('Li xa tin tc:', error) // In ra li nu c
      throw error // Nm li ra ngoi  x l
    }
  },

  // Upload nh
  async uploadImage(formData) {
    try {
      const response = await api.post('/news/upload', formData) // Gi yu cu POST  upload nh
      return response.data // Tr v d liu t phn hi
    } catch (error) {
      console.error('Li upload nh:', error) // In ra li nu c
      throw error // Nm li ra ngoi  x l
    }
  },

  // Khi phc tin tc
  async restoreNews(id) {
    try {
      // Gi yu cu PATCH khng km body data
      const response = await api.patch(`/news/${id}/restore`)
      return response.data // Tr v d liu t phn hi
    } catch (error) {
      console.error('Li khi phc tin tc:', error) // In ra li nu c
      throw error // Nm li ra ngoi  x l
    }
  },

  // Xa vnh vin tin tc
  async permanentDeleteNews(id) {
    try {
      const response = await api.delete(`/news/${id}/permanent`) // Gi yu cu DELETE  xa vnh vin tin tc
      return response.data // Tr v d liu t phn hi
    } catch (error) {
      console.error('Li xa vnh vin tin tc:', error) // In ra li nu c
      throw error // Nm li ra ngoi  x l
    }
  },

  // Ly tin tc trong thng rc
  async getNewsInTrash() {
    try {
      const response = await api.get('/news/trash') // Gi yu cu GET  ly tin tc trong thng rc
      return {
        data: response.data.data || [], // Tr v d liu tin tc, nu khng c th tr v mng rng
        pagination: response.data.pagination || null, // Tr v thng tin phn trang, nu khng c th tr v null
        message: response.data.message || '', // Tr v thng ip, nu khng c th tr v chui rng
      }
    } catch (error) {
      console.error('Li ly tin tc trong thng rc:', error) // In ra li nu c
      throw error // Nm li ra ngoi  x l
    }
  },
}

export default newsService // Xut i tng newsService  s dng  ni khc
      