// frontend/src/api/services/serviceService.js
import api from '../config'

const serviceService = {
  async getServices(params) {
    try {
      const response = await api.get('/services', { params })
      return {
        data: response.data.data || [], // D liu dch v
        pagination: response.data.pagination || null, // Thng tin phn trang
        message: response.data.message || '', // Thng bo t server
      }
    } catch (error) {
      console.error('Error fetching services:', error) // Ghi log li nu c
      throw error // Nm li ra ngoi
    }
  },

  async getServiceById(id) {
    try {
      const response = await api.get(`/services/${id}`) // Gi API  ly thng tin dch v
      return response.data.data // Tr v d liu dch v
    } catch (error) {
      console.error('Error fetching service details:', error) // Ghi log li nu c
      throw error // Nm li ra ngoi
    }
  },

  async createService(serviceData) {
    try {
      serviceData.description = Array.isArray(serviceData.description)
        ? serviceData.description
        : typeof serviceData.description === 'string'
          ? serviceData.description
              .split('\n')
              .map((line) => line.trim())
              .filter((line) => line.length >= 10)
          : ['M t dch v mc nh']

      if (!serviceData.description.length) {
        serviceData.description = ['M t dch v mc nh']
      }

      serviceData.type = serviceData.type || 'web'
      serviceData.title =
        serviceData.title?.trim() || 'Dch v mi ' + new Date().toLocaleDateString()

      console.log('Sending service data to backend:', serviceData)
      const response = await api.post('/services', serviceData)
      console.log('Service created successfully:', response.data)
      return response.data
    } catch (error) {
      console.error('Error creating service:', error); // Ghi log li nu c
      console.error('Error response:', error.response?.data); // Ghi log phn hi li t server
      throw error // Nm li ra ngoi
    }
  },

  // Cp nht thng tin dch v theo ID
  async updateService(id, serviceData) {
    try {
      // Chuyn i m t dch v thnh mng nu khng phi l mng
      serviceData.description = Array.isArray(serviceData.description)
        ? serviceData.description
        : typeof serviceData.description === 'string'
          ? serviceData.description.split('\n').map(line => line.trim()).filter(line => line.length >= 10)
          : ['M t dch v mc nh'];

      // Nu khng c m t, gn m t mc nh
      if (!serviceData.description.length) {
        serviceData.description = ['M t dch v mc nh'];
      }

      console.log('Updating service data:', serviceData); // Ghi log d liu dch v ang cp nht
      const response = await api.put(`/services/${id}`, serviceData); // Gi API  cp nht dch v

      // Kim tra phn hi t server
      if (!response.data.success) {
        throw new Error(response.data.message || 'Khng th cp nht dch v'); // Nm li nu khng thnh cng
      }

      console.log('Service updated successfully:', response.data); // Ghi log thng bo thnh cng
      return response.data // Tr v d liu phn hi
    } catch (error) {
      console.error('Error updating service:', error); // Ghi log li nu c
      throw new Error(error.response?.data?.message || 'Khng th cp nht dch v'); // Nm li ra ngoi
    }
  },

  // Xa dch v theo ID
  async deleteService(id) {
    try {
      const response = await api.delete(`/services/${id}`); // Gi API  xa dch v
      return response.data // Tr v d liu phn hi
    } catch (error) {
      console.error('Error deleting service:', error); // Ghi log li nu c
      throw error // Nm li ra ngoi
    }
  },

  // Khi phc dch v  xa
  async restoreService(id) {
    try {
      const response = await api.patch(`/services/${id}/restore`, { isDeleted: false }); // Gi API  khi phc dch v
      return response.data // Tr v d liu phn hi
    } catch (error) {
      console.error('Error restoring service:', error); // Ghi log li nu c
      throw error // Nm li ra ngoi
    }
  },

  // Xa vnh vin dch v
  async permanentDeleteService(id) {
    try {
      const response = await api.delete(`/services/${id}/permanent`); // Gi API  xa vnh vin dch v
      return response.data // Tr v d liu phn hi
    } catch (error) {
      console.error('Error permanently deleting service:', error); // Ghi log li nu c
      throw error // Nm li ra ngoi
    }
  },

  // Ti nh ln
  async uploadImage(formData) {
    try {
      const response = await api.post('/services/upload', formData, {
        maxContentLength: 5 * 1024 * 1024, // Gii hn kch thc ni dung
        maxBodyLength: 5 * 1024 * 1024, // Gii hn kch thc body
      });

      // Kim tra phn hi t server
      if (!response.data?.imagePath) {
        throw new Error('Khng nhn c ng dn nh t server'); // Nm li nu khng nhn c ng dn nh
      }

      return response.data // Tr v d liu phn hi
    } catch (error) {
      console.error('Error uploading image:', error); // Ghi log li nu c
      if (error.response?.status === 413) {
        throw new Error('Kch thc file qu ln, vui lng chn file nh hn 5MB'); // Nm li nu file qu ln
      }
      throw new Error(error.response?.data?.message || 'Khng th ti nh ln'); // Nm li ra ngoi
    }
  },
}

export default serviceService // Xut i tng serviceService  s dng  ni khc