import api from '../config'

// Dch v ti khon
const accountService = {
  // Hm tr gip  thc hin yu cu API
  async apiRequest(method, url, data = null, options = {}) {
    try {
      const config = {
        method,
        url,
        ...options,
      }

      if (data && method !== 'get') {
        config.data = data // Thm d liu vo cu hnh nu c v phng thc khng phi l GET
      }

      const response = await api(config) // Gi API vi cu hnh  nh ngha
      return {
        success: true,
        data: response.data.data || response.data, // Tr v d liu t phn hi
        message: response.data.message, // Tr v thng ip t phn hi
      }
    } catch (error) {
      // X l li c bn
      return {
        success: false,
        status: error.response?.status, // Tr v m trng thi li
        message: error.response?.data?.message || `Li khi thc hin yu cu`, // Thng ip li
      }
    }
  },

  // ng nhp admin
  async login(credentials) {
    try {
      const response = await api.post(`/admin/login`, credentials) // Gi yu cu ng nhp
      if (response.data.success) {
        localStorage.setItem('adminToken', response.data.token) // Lu token vo localStorage
        localStorage.setItem('adminInfo', JSON.stringify(response.data.admin)) // Lu thng tin admin
      }
      return response.data // Tr v d liu phn hi
    } catch (error) {
      return {
        success: false,
        message: error.response?.data?.message || 'Khng th ng nhp', // Thng ip li
      }
    }
  },

  // ng xut
  logout() {
    localStorage.removeItem('adminToken') // Xa token khi localStorage
    localStorage.removeItem('adminInfo') // Xa thng tin admin khi localStorage
  },

  // Ly thng tin admin ang ng nhp
  getAdminInfo() {
    const adminInfo = localStorage.getItem('adminInfo') // Ly thng tin admin t localStorage
    return adminInfo ? JSON.parse(adminInfo) : null // Tr v thng tin admin hoc null
  },

  // Kim tra  ng nhp cha
  isAuthenticated() {
    return !!localStorage.getItem('adminToken') // Tr v true nu  ng nhp
  },

  // Ly danh sch ti khon staff
  async getAccounts() {
    return this.apiRequest('get', '/admin/staffs') // Gi hm apiRequest  ly danh sch ti khon staff
  },

  // Ly chi tit mt ti khon
  async getAccountById(id) {
    return this.apiRequest('get', `/admin/staffs/${id}`) // Gi hm apiRequest  ly thng tin ti khon theo ID
  },

  // To ti khon mi
  async createAccount(accountData) {
    return this.apiRequest('post', '/admin/staffs', accountData) // Gi hm apiRequest  to ti khon mi
  },

  // Cp nht ti khon
  async updateAccount(id, accountData) {
    return this.apiRequest('put', `/admin/staffs/${id}`, accountData) // Gi hm apiRequest  cp nht ti khon
  },

  // Xa ti khon (soft delete)
  async deleteAccount(id) {
    return this.apiRequest('delete', `/admin/staffs/${id}`) // Gi hm apiRequest  xa ti khon
  },

  // Ly thng k dashboard
  async getDashboardStats() {
    return this.apiRequest('get', '/admin/dashboard') // Gi hm apiRequest  ly thng k dashboard
  },

  // Kim tra role admin
  isAdmin() {
    const adminInfo = this.getAdminInfo() // Ly thng tin admin
    return adminInfo?.role === 'admin' // Tr v true nu l admin
  },

  // Kim tra role staff
  isStaff() {
    const adminInfo = this.getAdminInfo() // Ly thng tin admin
    return adminInfo?.role === 'staff' // Tr v true nu l staff
  },

  // Ly tt c cc vai tr
  async getAccountRoles() {
    try {
      const response = await api.get(`/roles`) // Gi API  ly danh sch vai tr
      return {
        success: true,
        data: response.data, // Tr v d liu vai tr
      }
    } catch (error) {
      return {
        success: false,
        message: error.response?.data?.message || 'Khng th ti danh sch vai tr', // Thng ip li
      }
    }
  },

  // To vai tr mi
  async createAccountRole(roleData) {
    try {
      const response = await api.post(`/roles`, roleData) // Gi yu cu to vai tr mi
      return {
        success: true,
        data: response.data, // Tr v d liu vai tr mi
      }
    } catch (error) {
      return {
        success: false,
        message: error.response?.data?.message || 'Khng th to vai tr mi', // Thng ip li
      }
    }
  },

  // Cp nht vai tr
  async updateAccountRoles(id, roleData) {
    try {
      const response = await api.put(`/roles/${id}`, roleData) // Gi yu cu cp nht vai tr
      return {
        success: true,
        data: response.data, // Tr v d liu vai tr  cp nht
      }
    } catch (error) {
      return {
        success: false,
        message: error.response?.data?.message || 'Khng th cp nht vai tr', // Thng ip li
      }
    }
  },

  // Xa vai tr
  async deleteAccountRole(id) {
    try {
      const response = await api.delete(`/roles/${id}`) // Gi yu cu xa vai tr
      return {
        success: true,
        data: response.data, // Tr v d liu phn hi
      }
    } catch (error) {
      return {
        success: false,
        message: error.response?.data?.message || 'Khng th xa vai tr', // Thng ip li
      }
    }
  },

  // Ly quyn ca vai tr
  async getRolePermissions() {
    try {
      const response = await api.get(`/roles/permissions`) // Gi API  ly danh sch quyn hn
      return {
        success: true,
        data: response.data, // Tr v d liu quyn hn
      }
    } catch (error) {
      return {
        success: false,
        message: error.response?.data?.message || 'Khng th ti danh sch quyn hn', // Thng ip li
      }
    }
  },

  // Cp nht vai tr cho ti khon
  async updateAccountRoleAssignment(accountId, roles) {
    try {
      const response = await api.put(`/accounts/${accountId}/roles`, { roles }) // Gi yu cu cp nht vai tr cho ti khon
      return {
        success: true,
        data: response.data, // Tr v d liu phn hi
      }
    } catch (error) {
      return {
        success: false,
        message: error.response?.data?.message || 'Khng th cp nht vai tr cho ti khon', // Thng ip li
      }
    }
  },

  // Ly danh sch ti khon  xa
  async getDeletedAccounts() {
    try {
      const response = await api.get('/admin/deleted-staffs') // Gi API  ly danh sch ti khon  xa
      return {
        success: true,
        data: response.data.data || [], // Tr v d liu ti khon  xa
      }
    } catch (error) {
      return {
        success: false,
        message: error.response?.data?.message || 'Khng th ly danh sch ti khon  xa', // Thng ip li
        data: [],
      }
    }
  },

  // Khi phc ti khon (t isDeleted = false)
  async restoreAccount(id) {
    return this.apiRequest('put', `/admin/staffs/${id}`, { isDeleted: false }) // Gi hm apiRequest  khi phc ti khon
  },

  // Khi phc nhiu ti khon
  async restoreAccounts(ids) {
    try {
      const results = await Promise.all(ids.map((id) => this.restoreAccount(id))) // Khi phc tng ti khon
      const failedCount = results.filter((r) => !r.success).length // m s ti khon khng khi phc c

      if (failedCount > 0) {
        return {
          success: false,
          message: `Khng th khi phc ${failedCount} ti khon`, // Thng ip li
        }
      }

      return {
        success: true,
        message: ` khi phc ${ids.length} ti khon thnh cng`, // Thng ip thnh cng
      }
    } catch (error) {
      return {
        success: false,
        message: error.message || 'Khng th khi phc ti khon', // Thng ip li
      }
    }
  },

  // Xa vnh vin ti khon
  async permanentDeleteAccount(id) {
    try {
      const response = await api.delete(`/admin/staffs/${id}?permanent=true`) // Gi yu cu xa vnh vin ti khon
      return {
        success: response.data.success,
        message: response.data.message || ' xa vnh vin ti khon thnh cng', // Thng ip thnh cng
      }
    } catch (error) {
      return {
        success: false,
        message: error.response?.data?.message || 'Khng th xa vnh vin ti khon', // Thng ip li
      }
    }
  },

  // Xa vnh vin nhiu ti khon
  async permanentDeleteAccounts(ids) {
    try {
      const results = await Promise.all(ids.map((id) => this.permanentDeleteAccount(id))) // Xa vnh vin tng ti khon
      const failedCount = results.filter((result) => !result.success).length // m s ti khon khng xa c

      if (failedCount > 0) {
        return {
          success: false,
          message: `Khng th xa vnh vin ${failedCount}/${ids.length} ti khon`, // Thng ip li
        }
      }

      return {
        success: true,
        message: ` xa vnh vin ${ids.length} ti khon thnh cng`, // Thng ip thnh cng
      }
    } catch (error) {
      return {
        success: false,
        message: error.message || 'Khng th xa vnh vin ti khon', // Thng ip li
      }
    }
  },
}

export default accountService // Xut dch v ti khon
