import axios from 'axios'

// Cu hnh kt ni n backend
// Kim tra mi trng  s dng URL ph hp
const isProduction = process.env.NODE_ENV === 'production'

// Base URL cho API requests
const baseApiUrl = isProduction 
  ? 'https://sdtc-website.onrender.com/api'  // m bo c /api  cui
  : 'http://localhost:3000/api'

// Base URL cho media (hnh nh, video, vv...)
export const baseMediaUrl = isProduction
  ? 'https://sdtc-website.onrender.com'  // URL t Render dashboard
  : 'http://localhost:3000'

const api = axios.create({
  baseURL: baseApiUrl,
  timeout: 10000, // Timeout 10 giy
  headers: {
    'Content-Type': 'application/json',
  },
  // Thm withCredentials  gi cookies nu cn
  withCredentials: false
})

// Thm interceptor cho yu cu
api.interceptors.request.use(
  (config) => {
    const token = localStorage.getItem('adminToken')
    if (token) {
      config.headers.Authorization = `Bearer ${token}`
    }
    // For FormData, let the browser set the Content-Type
    if (config.data instanceof FormData) {
      delete config.headers['Content-Type']
    }
    return config
  },
  (error) => {
    return Promise.reject(error)
  }
)
// Add a response interceptor
api.interceptors.response.use(
  (response) => response,
  (error) => {
    if (error.response?.status === 401) {
      localStorage.removeItem('adminToken')
      localStorage.removeItem('adminInfo')
      window.location.href = '/admin/dashboard'
    }
    return Promise.reject(error)
  }
)

export default api
